import {React, useEffect } from "react";
import './Card.css';
import { Link, useParams } from 'react-router-dom';
import { useDispatch, useSelector } from "react-redux";
import { getRecipeDetail } from "../actions/actions";


export default function RecipeDetail(){

let {id} = useParams();

const dispatch = useDispatch();
const recipeDetail = useSelector((state) => state.recipe);
  
console.log(`Renderizando detail of recipe id: ${id} y ${recipeDetail}`)
  
useEffect(() => {
    console.log(recipeDetail);
    dispatch(getRecipeDetail(id));
    console.log(recipeDetail)
   }, [dispatch]);  
    
    return (
        
        <div className='card'>
            <Link to = '/home'>
                <button>Back page</button>
            </Link>

            <div>
                <img src= {recipeDetail.image} alt="PhotoHere" />
            </div>
                <div className='card-title'>
            <h1>{recipeDetail.title}</h1>
            </div>
            
            <div className='card-body'>
                <h2>HealthScore: {recipeDetail.healthScore}</h2>
                <p>Diets: {recipeDetail.diets.join(', ')}</p>

                <h4>Step by Step</h4>
                <ul>{recipeDetail.stepBYstep.map((step) => {return (<li>{step}</li>)})}
                </ul>

            </div>       
       </div>
    );
};